<template>
  <div class="main-container">
    <button @click="toggleSidebar" class="toggle-button">
      <i :class="isSidebarOpen ? 'fas fa-times' : 'fas fa-bars'"></i>
    </button>

    <transition name="slide-fade">
      <SideBar v-if="isSidebarOpen" />
    </transition>

    <div class="main-content">
      <h2>Main Content</h2>
      <p>This is the main content area where the core content will go.</p>
    </div>
  </div>
</template>

<script>
import SideBar from './SideBar.vue';

export default {
  name: 'MainContainer',
  components: { SideBar },
  data() {
    return {
      isSidebarOpen: true, // Sidebar starts open
    };
  },
  methods: {
    toggleSidebar() {
      this.isSidebarOpen = !this.isSidebarOpen;
    },
  },
};
</script>

<style scoped>
.main-container {
  display: flex;
  flex: 1;
  position: relative;
}

.toggle-button {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 10;
  background-color: #333;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.toggle-button i {
  font-size: 24px; /* Adjust the icon size */
}

.main-content {
  flex: 1;
  padding: 2rem;
  background-color: #e9e9e9;
}
</style>
